import React, { useEffect, useState } from 'react';
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import Divider from "@mui/material/Divider";
import Skeleton from "@mui/material/Skeleton";
import axios from 'axios';

function MarketNews() {
  const [newsPosts, setNewsPosts] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Replace the URL with your API endpoint for fetching market news
    axios.get('http://localhost:3001/marketnews')
      .then(response => {
        setNewsPosts(response.data);
        setLoading(false);
      })
      .catch(error => {
        console.error("Error:", error);
        setLoading(false);
      });
  }, []);

  const getImagePath = (index) => {
    const imageIndex = index % 7; // To loop among 0.png, 1.jpg, 2.png, etc.
    return `/blog/${imageIndex}.png`; // Adjust the path as needed
  };

  return (
    <List>
      {loading ? (
        Array.from({ length: 5 }).map((_, index) => (
          <React.Fragment key={`skeleton-${index}`}>
            <ListItem>
              <img
                  src="/blog/skeleton-icon.png"
                  alt="Skeleton Icon"
                  style={{
                    display: { xs: 'none', md: 'flex' },
                    marginRight: '10px',
                    width: '28px',
                    height: '28px',
                  }}
                />
              <ListItemText>
                <Skeleton animation="wave" height={30} />
              </ListItemText>
            </ListItem>
            {index !== 4 && <Divider orientation="horizontal" flexItem />}
          </React.Fragment>
        ))
      ) : (
        newsPosts.map((post, index) => (
          <React.Fragment key={`post-${index}`}>
            <ListItem button component="a" href={post.link} target="_blank" rel="noopener noreferrer">
              <img
                src={getImagePath(index)}
                alt="News Icon"
                style={{
                  display: { xs: 'none', md: 'flex' },
                  marginRight: '1rem',
                  width: '28px', // Adjust the width and height as needed
                  height: '28px',
                  backgroundColor: '#343C4F',
                  borderRadius: '50%',
                  padding: '2px',
                }}
              />
              <ListItemText primary={post.headline} primaryTypographyProps={{ style: { fontSize: '14px' } }} />
            </ListItem>
            {index !== newsPosts.length - 1 && <Divider orientation="horizontal" flexItem />}
            <Divider orientation="horizontal" flexItem style={{ backgroundColor: '#343C4F' }} />
          </React.Fragment>
        ))
      )}
    </List>
  );
}

export default MarketNews;
