import React from 'react'
import { Grid } from '@mui/material'
import Blogcards from "./Blogcards";
import MoneyControl from "./MoneyControl";
import Box from "@mui/material/Box";
import List from "@mui/material/List";
export default function LeftGrid() {
  return (
    <Grid
    item
    justifyContent="center"
    alignItems="center"
    // lg={3}
    // md = {4}
    xs={12}
    spacing={0}
    
    container
    sx={{ height: "98vh" ,  display: { xs: 'block', lg: 'none' , marginTop : '-10px'} }}
    
  >
    <Grid
      item
      xs={12}
      sx={{
        background: "#111A30",
        height: "47.5%",
        overflow: 'hidden',
        borderRadius: "10px",
        padding: "8px",
       
      }}
    >
      <p className="mb-2  font-bold text-zinc-400">Moneycontrol News</p>
      <Box className="scrollbox" >
        <List
          sx={{
            position: "relative",
            overflow: "auto",
            maxHeight: 300,
            "& ul": { padding: 0 },
          }}
          subheader={<li />}
        >
          <MoneyControl/>
        
          
        </List>
      </Box>
    </Grid>
    <Grid
      item
      xs={12}
      sx={{
        height: "47.5%",
        background: "#111A30",
        borderRadius: "10px",
        padding: "8px",
        overflow: "hidden",
        marginTop : '10px'
      }}
    >
      <p className="mb-2 font-bold text-zinc-400">Blogs</p>
     <Box className="scrollbox">
        <List
          sx={{
            position: "relative",
            overflow: "auto",
            maxHeight: 300,
            "& ul": { padding: 0 },
           
          }}
          subheader={<li />}
        >
          <Blogcards/>
        </List>
      </Box>
    </Grid>
  </Grid>
  )
}


