import React, { useState, useEffect } from "react";
import Typography from "@mui/material/Typography";
import { useNavigate } from "react-router-dom";
import "../App.css";
import Grid from "@mui/material/Grid";
import TextField from "@mui/material/TextField";
import CircularProgress from "@mui/material/CircularProgress";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";
import Button from "@mui/material/Button";
import Chip from "@mui/material/Chip";

function getStoredToken() {
  return localStorage.getItem("jwtToken"); // Replace 'jwtToken' with the actual key you used for storing the token
}

const darkTheme = createTheme({
  palette: {
    mode: "dark",
  },
});

function Assets() {
  const navigate = useNavigate();
  const [userData, setUserData] = useState(null);
  const token = getStoredToken();
  const [amount, setAmount] = useState("");

  // Calculate the number of days between two dates
  const calculateDaysElapsed = (startDate, endDate) => {
    const oneDay = 24 * 60 * 60 * 1000; // hours * minutes * seconds * milliseconds
    const diffDays = Math.round(Math.abs((startDate - endDate) / oneDay));
    return diffDays;
  };
  // Calculate the number of months between two dates
  const calculateMonthsElapsed = (startDate, endDate) => {
    const startYear = startDate.getFullYear();
    const startMonth = startDate.getMonth();
    const endYear = endDate.getFullYear();
    const endMonth = endDate.getMonth();
    
    return (endYear - startYear) * 12 + (endMonth - startMonth);
  };

  // Calculate interest earned with a 1% monthly return
  const calculateInterestEarned = (principal, months) => {
    const monthlyReturn = 0.01; // 1% monthly return
    let totalAmount = principal;
    
    for (let i = 0; i < months; i++) {
      totalAmount *= (1 + monthlyReturn);
    }
    
    return totalAmount - principal;
  };

  useEffect(() => {
    if (token) {
      fetch("http://localhost:3001/userapi", {
        headers: { Authorization: `Bearer ${token}` },
      })
        .then((response) => {
          if (!response.ok) {
            throw new Error("Network response was not ok");
          }
          return response.json();
        })
        .then((data) => {
          setUserData(data);
          console.log(userData);
        })
        .catch((error) => {
          console.error("Fetch error:", error);
        });
    } else {
      navigate("/Login");
    }
  }, [token]);

  const handleDepositClick = async () => {
    if (amount !== "") {
      try {
        await fetch("http://localhost:3001/users/deposit", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ username: userData.username, amount: parseFloat(amount) }),
        });

        // Fetch updated user data
        const response = await fetch("http://localhost:3001/userapi", {
          headers: { Authorization: `Bearer ${token}` },
        });

        if (response.ok) {
          const data = await response.json();
          setUserData(data); // Update the user data with the new data
        } else {
          throw new Error("Network response was not ok");
        }
      } catch (error) {
        console.error("Deposit error:", error);
      }
    }
  };
  
  const handleWithdrawClick = async () => {
    if (amount !== "") {
      try {
        await fetch("http://localhost:3001/users/withdraw", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ username: userData.username, amount: parseFloat(amount) }),
        });

        // Fetch updated user data
        const response = await fetch("http://localhost:3001/userapi", {
          headers: { Authorization: `Bearer ${token}` },
        });

        if (response.ok) {
          const data = await response.json();
          setUserData(data); // Update the user data with the new data
        } else {
          throw new Error("Network response was not ok");
        }
      } catch (error) {
        console.error("Withdraw error:", error);
      }
    }
  };
  
  const handleSubscriptionUpdate = async (newSubscriptionType) => {
    try {
      await fetch("http://localhost:3001/users/updatesubscription", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ username: userData.username, subscriptionType: newSubscriptionType }),
      });

      // Fetch updated user data
      const response = await fetch("http://localhost:3001/userapi", {
        headers: { Authorization: `Bearer ${token}` },
      });

      if (response.ok) {
        const data = await response.json();
        setUserData(data); // Update the user data with the new data
      } else {
        throw new Error("Network response was not ok");
      }

      // Assuming the response indicates success somehow
      console.log("Subscription updated successfully");
    } catch (error) {
      console.error("Subscription update error:", error);
    }
  };

  if (!userData) {
    return (
      <div style={{ display: "flex", justifyContent: "center", alignItems: "center", height: "100vh" }}>
        <CircularProgress size={50} style={{ color: "#90CAF9" }} />
      </div>
    );
  }
  
  return (
    <ThemeProvider theme={darkTheme}>
      <CssBaseline />
      <div className="App flex items-center justify-center p-4 bg-App">
        <Grid
          className="parent"
          container
          direction="row"
          justifyContent="center"
          alignItems="center"
          spacing={2}
          sx={{ height: "90vh", width: "80vw", margin: "0px" }}
        >
          <div className="w-full md:w-1/2  h-4/5   rounded-md bg-Main">
            <p className="m-4 text-xl font-bold">ADD MONEY</p>

            <div className="flex flex-row justify-around items-center ">
              <div className="text-center">Enter the Amount</div>
              <div className="flex justify-center">
              <TextField
                id="outlined-basic"
                label="₹"
                variant="outlined"
                className="  h-4/5"
                value={amount}
                onChange={(event) => setAmount(event.target.value)}
                sx={{
                  width: {
                    xs: '80%',
                    md: '90%'
                  }
                }}
              />
              </div>
            </div>

            {/* Add margin to this div */}
            <div className="flex flex-col justify-center items-center mt-4" style={{marginTop:'15px'}}>
              <span className="text-slate-500">Current Balance ⓘ</span>
              <Typography variant="h6">₹ {userData.money}</Typography>
            </div>

            <div className="flex  flex-col  md:flex-row justify-around items-center ">
            <Chip
              label="Monthly Plan - ₹ 600/month"
              variant="contained"
              className=" bg-Button mr-2 mt-10 m-3"
              sx={{ marginTop: '10px', width : {xs : '90%' , md : '70%' }}}
              onClick={() => handleSubscriptionUpdate('M')}
              clickable
            />

            <Chip
              label="Yearly Plan - ₹ 7200/year"
              variant="contained"
              className=" bg-Button ml-2"
              sx={{ marginTop: '10px', width: {  xs: '90%',md: '70%'}}}
              onClick={() => handleSubscriptionUpdate('Y')}
              clickable
            />
            </div>

            <div className="flex flex-col justify-center items-center  text-center mt-4" style={{ marginTop: '15px' }}>
              {userData.subscriptionType === 'N' ? (
                <span className="text-slate-500">User has not subscribed to any Plan.</span>
              ) : userData.subscriptionType === 'M' ? (
                <>
                  <span className="text-slate-500">
                    User has subscribed to Monthly Plan.{' '}
                    {userData.subscriptionAt &&
                      `(${calculateMonthsElapsed(new Date(userData.subscriptionAt), new Date())} months elapsed)`}
                  </span>
                  <Typography variant="h7">
                    Interest Earned: ₹ {calculateInterestEarned(userData.money, calculateMonthsElapsed(new Date(userData.subscriptionAt), new Date())).toFixed(2)}
                  </Typography>
                </>
              ) : userData.subscriptionType === 'Y' ? (
                <>
                  <span className="text-slate-500">
                    User has subscribed to Yearly Plan.{' '}
                    {userData.subscriptionAt &&
                      `(${calculateMonthsElapsed(new Date(userData.subscriptionAt), new Date())} months elapsed)`}
                  </span>
                  <Typography variant="h7">
                    Interest Earned: ₹ {calculateInterestEarned(userData.money, calculateMonthsElapsed(new Date(userData.subscriptionAt), new Date())).toFixed(2)}
                  </Typography>
                </>
              ) : null}
            </div>

           <div className="flex flex-col justify-center items-center "  style={{marginTop : "15px"}}>
              <Button
                variant="contained"
                className="w-4/5 bg-Button mr-2 mt-10"
                onClick={handleDepositClick}
                sx={{marginTop:'10px'}}
              >
                DEPOSIT MONEY
              </Button>

              <Button
                variant="contained"
                className="w-4/5 bg-Button ml-2"
                sx={{marginTop:'10px'}}
                onClick={handleWithdrawClick}
              >
                WITHDRAW MONEY
              </Button>
              </div>
          </div>
        </Grid>
      </div>
    </ThemeProvider>
  );
}

export default Assets;