import React, { useEffect, useState } from 'react';
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import ListItemIcon from "@mui/material/ListItemIcon"; // Import ListItemIcon
import Divider from "@mui/material/Divider";
import Skeleton from "@mui/material/Skeleton";
import AccountBalanceIcon from '@mui/icons-material/AccountBalance';

function MoneyControl() {
  const [moneyNews, setNews] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch(`http://localhost:3001/scrape`)
      .then((response) => response.json())
      .then((data) => {
        setNews(data); // Update the state with fetched data
        setLoading(false); // Set loading to false after data is fetched
      })
      .catch((error) => console.error("Error:", error));
  }, []);

  return (
    <>
      <List>
        {loading ? (
          // Show skeleton while loading
          Array.from({ length: 5 }).map((_, index) => (
            <React.Fragment key={`skeleton-${index}`}>
              <ListItem >
                <ListItemIcon>
                  <AccountBalanceIcon /> {/* Replace with the imported icon */}
                </ListItemIcon>
                <ListItemText>
                  <Skeleton animation="wave" height={30} />
                </ListItemText>
              </ListItem>
              {index !== 4 && <Divider orientation="horizontal" flexItem />}
            </React.Fragment>
          ))
        ) : (
          // Show data after loading is complete
          moneyNews.map((entry, index) => (
            <React.Fragment key={`item-${index}`}>
              <ListItem button component="a" href={entry.link} target="_blank" rel="noopener noreferrer">
                <img
                  src="/blog/0.png" // Replace this with the actual path to your custom image
                  alt="Smartvitta"
                  style={{
                    display: { xs: 'none', md: 'flex' },
                    marginRight: '1rem',
                    width: '42px', // Adjust the width and height as needed
                    height: '42px',
                    backgroundColor: '#343C4F',
                    borderRadius: '50%',
                    padding: '4px',
                  }}
                />
                <ListItemText primary={entry.headline} primaryTypographyProps={{ style: { fontSize: '14px' } }} />
              </ListItem>
              {index !== moneyNews.length - 1 && <Divider orientation="horizontal" flexItem />}
              <Divider orientation="horizontal" flexItem style={{ backgroundColor: '#343C4F' }}/>
            </React.Fragment>
          ))
        )}
      </List>
    </>
  );
}

export default MoneyControl;
