import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box'; // Import the Box component
import Container from '@mui/material/Container'; // Import the Container component
import "../App.css";
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import { ThemeProvider, createTheme } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";
import Snackbar from '@mui/material/Snackbar';
const darkTheme = createTheme({
  palette: {
    mode: "dark",
  },
});


function AdminDashboard() {
  const navigate = useNavigate();
  const [adminview, setAdminview] = useState(false);
  const [blogContent, setBlogContent] = useState('');
  const [blogUrl, setBlogUrl] = useState('');
  const [videoUrl, setVideoUrl] = useState('')
  const [quote , setQuote] = useState('')

  const [openBlog, setOpenBlog] = useState(false)

  useEffect(() => {
    const admintoken = localStorage.getItem('jwtToken');
    console.log('admin token is', admintoken);
    if (admintoken) {
      axios.post('http://localhost:3001/users/adminverify', {
        jwtToken: admintoken
      })
      .then((response) => {
        console.log(response.data);
        if (response.data.adminroute) {
          setAdminview(true);
        } else {
          setAdminview(false);
          navigate('/');
        }
      })
      .catch((error) => {
        console.log('error in the admin dashboard route is', error);
        // navigate('/Login');
      });
    } else {
      navigate('/Login');
    }
  }, []); // Empty dependency array to ensure the effect runs only once on component mount



  const dashboardView = () =>{
    navigate('/')
  }
  

  const handleBlogSubmit = () => {
    // Send a POST request to save the blog in the database
    axios.post('http://localhost:3001/blogs/saveblog', {
      content: blogContent,
      url: blogUrl,
    })
    .then((response) => {
      // Handle success, e.g., show a success message or refresh data
      console.log('Blog saved successfully');
      // Clear the input fields
      setBlogContent('');
      setBlogUrl('');
      setOpenBlog(true)
      setTimeout(()=>{
        setOpenBlog(false)
      },2000)
    })
    .catch((error) => {
      // Handle error, e.g., show an error message
      console.log('Error saving blog:', error);
    });
  };


  const videoSubmit = () =>{
    axios.post('http://localhost:3001/videos/savevideo', {
      url: videoUrl,
    })
    .then((response) => {
      // Handle success, e.g., show a success message or refresh data
      console.log('video saved successfully');
      // Clear the input fields
      setVideoUrl('')
      setOpenBlog(true)
      setTimeout(()=>{
        setOpenBlog(false)
      },2000)
    })
    .catch((error) => {
      // Handle error, e.g., show an error message
      console.log('Error saving Videp:', error);
    });
  }


  const quoteSubmit = ()=>{
    axios.post('http://localhost:3001/quote/savequote', {
      inputQuote : quote,
    })
    .then((response) => {
      // Handle success, e.g., show a success message or refresh data
      console.log('quote saved successfully');
      // Clear the input fields
      setQuote('')
      setOpenBlog(true)
      setTimeout(()=>{
        setOpenBlog(false)
      },2000)
    })
    .catch((error) => {
      // Handle error, e.g., show an error message
      console.log('Error saving Videp:', error);
    });
  }




 

  return (
    // Return JSX
    <ThemeProvider theme={darkTheme}>
    <CssBaseline />
    <div>
      {adminview ? (
        <Box className="App" >
          <Container component="main" maxWidth={false}>
            <Grid container spacing={2} sx={{ height: "100%"  }}>
             

    <Grid
  container
  direction="column"
  justifyContent='center'
  alignItems="center"
  xs={12}
  sx={{ height: "95vh" ,  marginTop : '10px', width : '80vw'}}
>
 

{/* blogs */}
  <Box gridColumn="span 12"  sx={{backgroundColor : '#111A30',height : '25%',width: '98%',marginTop : '15px', padding : '10px',borderRadius : '10px'}}>
   <p>ADD BLOGS</p>
   <div className='flex flex-col justify-center items-center'>
   <TextField id="standard-basic" label="Write your todays blog" variant="standard"  onChange={(e) => setBlogContent(e.target.value)}  value={blogContent}   sx={{width : '90%'}} required={true}/>
   <div className='flex flex-row justify-between' style={{width:'90%'}}>
   <TextField id="standard-basic" label="Add URL" variant="standard"  onChange={(e) => setBlogUrl(e.target.value)}  value={blogUrl}  sx={{width : '80%'}}  required={true}/>
   <Button variant="outlined" type="submit"  onClick={handleBlogSubmit}   sx={{marginTop : '20px'}}>
                  Submit
    </Button>
   </div>
   

   </div>
  </Box>


{/* videos */}
  <Box gridColumn="span 12" sx={{backgroundColor : '#111A30',height : '20%',width: '98%', marginTop : '15px',padding :'10px',borderRadius : '10px'}}>
  <p>ADD VIDEOS</p>
   <div className='flex flex-col justify-center items-center'>
   <div className='flex flex-row justify-between' style={{width:'90%'}}>
   <TextField id="standard-basic" label="Add URL" variant="standard"  onChange={(e) => setVideoUrl(e.target.value)}  value={videoUrl}  sx={{width : '80%'}} required={true}/>
   <Button variant="outlined" type="submit" onClick={videoSubmit}  sx={{marginTop : '20px'}}>
                  Submit
    </Button>
    </div>
</div>
  </Box>


{/* quotes */}
  <Box gridColumn="span 12" sx={{backgroundColor : '#111A30',height : '20%',width: '98%' , marginTop : '15px' ,padding :'10px',borderRadius : '10px'}}>
  <p >ADD QUOTES</p>
   <div className='flex flex-col justify-center items-center'>
   <div className='flex flex-row justify-between' style={{width:'90%'}}>
   <TextField id="standard-basic" label="Your Quotes" variant="standard"  onChange={(e) => setQuote(e.target.value)}  value={quote}  sx={{width : '80%'}} required={true}/>
   <Button variant="outlined" type="submit" onClick={quoteSubmit}  sx={{marginTop : '20px'}}>
                  Submit
  </Button>
  </div>
</div>
  </Box>

  <Button variant="contained" type="submit"  onClick={dashboardView}  sx={{marginTop : '20px'}}>
                  View
                </Button>

  </Grid>

  <Snackbar 
        open={openBlog}
        autoHideDuration={6000}
        message="Added Successfully"
      />


            </Grid>
          </Container>
        </Box>
      ) : (
        <></>
      )}
    </div>
    </ThemeProvider>
  );
}

export default AdminDashboard;
