import Typography from "@mui/material/Typography";
import "../App.css";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";

const darkTheme = createTheme({
  palette: {
    mode: "dark",
  },
});

const About = () => {
  const name = "Sara";
  return (
    <ThemeProvider theme={darkTheme}>
      <CssBaseline />
      <div className="App flex items-center justify-center p-4 bg-App">
        <Grid
          className="parent"
          container
          direction="column"
          justifyContent="center"
          alignItems="center"
          // spacing={}
          sx={{
            height: { sm: "80vh", md: "70vh" },
            width: "85vw",
            margin: "0px",
            backgroundColor: "#111A30",
            padding: "0 20px",
          }}
        >
          
          <img src="/smartvitta.png" alt="SmartVitta" style={{ marginBottom: '20px', width: '50px' }} />

          <div className="w-4/5 h-3/6 md:h-3/5 flex   flex-wrap justify-around items-center">
            {/* <Typography variant="h4" style={{marginBottom : '20px'}}> SmartVitta </Typography> */}

            <div className='text-center'>
              <Typography variant="body1" sx={{ fontSize: '1.1rem', marginBottom: '1rem' }}>
              SmartVitta is designed with the understanding that many individuals lack the necessary financial knowledge to navigate the complexities of investing, budgeting, and managing money effectively. Simultaneously, there is a growing desire to explore opportunities for earning through various channels. The platform aims to empower users by providing accessible, comprehensive, and user-friendly resources to learn about finance while also offering avenues for easy earnings
              </Typography>
              <br />
              <Typography variant="body1" sx={{ fontSize: '1.1rem', marginBottom: '1rem' }}>
              As SmartVitta continues to evolve, it has the potential to revolutionize the way people approach finance and earnings. The platform's growth could involve partnerships with financial institutions, expanding into international markets, and incorporating cutting-edge technologies such as blockchain for secure transactions and investments.
              </Typography>
            </div>
          </div>
        </Grid>
      </div>
    </ThemeProvider>
  );
};

export default About;
