import React, { useState, useEffect } from 'react';
import UserProfile from './UserProfile';
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import Typography from '@mui/material/Typography';
import Skeleton from '@mui/material/Skeleton';
import Tooltip from '@mui/material/Tooltip';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Button from '@mui/material/Button';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';

function getStoredToken() {
  return localStorage.getItem("jwtToken");
}

function DashInfoCard({ username }) {
  const [anchorElUser, setAnchorElUser] = useState(null);
  const [userData, setUserData] = useState(null);
  const [loading, setLoading] = useState(true);
  const token = getStoredToken();
  const navigate = useNavigate();

  const handleOpenUserMenu = (event) => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  const handleLogout = () => {
    handleCloseUserMenu();
    localStorage.removeItem('jwtToken');
    localStorage.removeItem('USERID');
    navigate('/nothing');
  };

  useEffect(() => {
    if (token) {
      fetch("http://localhost:3001/userapi", {
        headers: { Authorization: `Bearer ${token}` },
      })
        .then((response) => {
          if (!response.ok) {
            throw new Error("Network response was not ok");
          }
          return response.json();
        })
        .then((data) => {
          setUserData(data);
          setLoading(false);
        })
        .catch((error) => {
          console.error("Fetch error:", error);
        });
    }
  }, [token]);

  if (!token) {
        return (
          <MenuItem
            onClick={handleCloseUserMenu}
            component={Link}
            to='/Login'
            // sx={{
            //   backgroundColor: '#111A30',
            //   border: '1px solid rgba(255, 255, 255, 0.15)',
            //   borderRadius: '10px',
            // }}
          >
            {/* <butt textAlign="center">Login</butt> */}
            <Button variant="contained"  sx={{ backgroundColor: '#233A71' , textTransform: 'none'}}>Login</Button>
          </MenuItem>
    )
  }
  return (
    <>
    <div className='nameAssets'>
      <div className='flex flex-row justify-between items-center' style={{ backgroundColor: '#161D2D', width: 'auto', borderRadius: '999px' }}>
        <div style={{  borderRadius: '999px', border : '3px solid #5267FD'   ,  display : 'flex' , flexDirection : 'row' , alignItems : 'center' , justifyContent : 'center'}}>
          <UserProfile />
        </div>
        {loading ? (
          <Skeleton variant="text" width={60} height={20} />
        ) : (
          <Typography 
            textAlign="center" 
            style={{ cursor: 'pointer', display: 'flex', alignItems: 'center' }} 
            sx={{ px: 1.5 }}
          >
            ₹ {userData?.money || 0}
          </Typography>
        )}
      </div>

      </div>
      <Tooltip title="Show more">
        {token ? (
          <Typography
            onClick={handleOpenUserMenu}
            style={{ cursor: 'pointer', display: 'flex', alignItems: 'center' }}
            textAlign="center"
            sx={{ p: 1.5 }} // Add top and bottom margins
          >
            {userData?.username || 'Username'}
            <ArrowDropDownIcon style={{ marginLeft: '5px' }} />
          </Typography>
        ) : (
          <Link to="/Login">
            <Typography textAlign="center">Login</Typography>
          </Link>
        )}
      </Tooltip>
      <Menu
        sx={{ mt: '45px' }}
        id="menu-appbar"
        anchorEl={anchorElUser}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        keepMounted
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        open={Boolean(anchorElUser)}
        onClose={handleCloseUserMenu}
      >
        {token ? (
          <>
            <MenuItem component={Link} onClick={handleCloseUserMenu} to='/Profile'>
              <Typography textAlign="center">Profile</Typography>
            </MenuItem>
            <MenuItem component={Link} onClick={handleCloseUserMenu} to='/Assets'>
              <Typography textAlign="center">Assets</Typography>
            </MenuItem>
            <MenuItem component={Link} onClick={handleLogout} to='/Login'>
              <Typography textAlign="center">Logout</Typography>
            </MenuItem>
          </>
        ) : (
          <>
            <MenuItem onClick={handleCloseUserMenu} component={Link} to='/Login'>
              <Typography textAlign="center">Login</Typography>
            </MenuItem>
          </>
        )}
      </Menu>
    </>
  );
}

export default DashInfoCard;
