import React, { useState, useEffect } from "react";
import { useNavigate } from 'react-router-dom';
import "../App.css";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Button from '@mui/material/Button';
import TextField from "@mui/material/TextField";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import CircularProgress from "@mui/material/CircularProgress";
import CssBaseline from "@mui/material/CssBaseline";
import BigProfile from "../components/BigProfile";
import UsermProfile from "../components/UsermProfile";
import axios from 'axios';

function getStoredToken() {
  return localStorage.getItem("jwtToken"); // Replace 'jwtToken' with the actual key you used for storing the token
}

const darkTheme = createTheme({
  palette: {
    mode: "dark",
  },
});

function Profile() {
  const navigate = useNavigate();
  const [userData, setUserData] = useState(null);
  const token = getStoredToken(); // Retrieve the stored token
  const [username, setUsername] = useState(''); // Add username state
  const [selectedFile, setSelectedFile] = useState(null);

  const handleFileChange = (event) => {
    setSelectedFile(event.target.files[0]);
  };

  const handleUpload = async () => {
    console.log("uploading file");
    const formData = new FormData();
    formData.append('avatar', selectedFile);
    formData.append('username', username);
    try {
      await axios.post('http://localhost:3001/avatar/upload', formData);
      // Display a success message or update user data in the state
    } catch (error) {
      // Handle error
    }
  }

    useEffect(() => {
    if (token) {
      fetch("http://localhost:3001/userapi", {
        headers: { Authorization: `Bearer ${token}` },
      })
        .then((response) => {
          if (!response.ok) {
            throw new Error("Network response was not ok");
          }
          return response.json();
        })
        .then((data) => {
          setUserData(data);
          console.log(data.username);
          setUsername(data.username);
        })
        .catch((error) => {
          console.error("Fetch error:", error);
        });
    } else {
      navigate("/Login");
    }
  }, [token]);

  if (!userData) {
    return (
      <div style={{ display: "flex", justifyContent: "center", alignItems: "center", height: "100vh" }}>
        <CircularProgress size={50} style={{ color: "#90CAF9" }} />
      </div>
    );
  }
    return (
      <ThemeProvider theme={darkTheme}>
        <CssBaseline />
        <div className=" App flex items-center justify-center p-4 bg-App">
          <Grid
            className="parent"
            container
            direction="row"
            justifyContent="center"
            alignItems="center"
            spacing={2}
            sx={{ height: "80vh", width: "85vw", margin: "0px" }}
          >
            {/* first grid */}
            <Grid
              item
              sm={5}
              xs={12}
              sx={{
                backgroundColor: "#111A30",
                height: "100%",
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
                alignItems: "center",
                overflow: "scroll",
              }}
            >
              <Box
                component="form"
                sx={{
                  "& .MuiTextField-root": { m: 1.5, width: "80%" },
                }}
                noValidate
                autoComplete="off"
              >
                <div>

                  <div className="flex flex-row justify-center items-center   sm:hidden" >
                  <UsermProfile username={username}/>
                
                  </div>

                  <TextField
                    id="standard-read-only-input"
                    label="Name"
                    defaultValue={userData.firstName + " " + userData.lastName}
                    InputProps={{
                      readOnly: true,
                    }}
                    variant="standard"
                  />
                  <TextField
                    id="standard-read-only-input"
                    label="Email"
                    defaultValue={userData.email}
                    InputProps={{
                      readOnly: true,
                    }}
                    variant="standard"
                  />
                  <TextField
                    id="standard-read-only-input"
                    label="Mobile Number"
                    defaultValue={userData.phoneNumber}
                    InputProps={{
                      readOnly: true,
                    }}
                    variant="standard"
                  />
                  <TextField
                    id="standard-read-only-input"
                    label="PAN"
                    defaultValue={userData.panNumber}
                    InputProps={{
                      readOnly: true,
                    }}
                    variant="standard"
                  />
                  <TextField
                    id="standard-read-only-input"
                    label="Courses Completed"
                    defaultValue={userData.coursesCompleted}
                    InputProps={{
                      readOnly: true,
                    }}
                    variant="standard"
                  />
                  <TextField
                    id="standard-read-only-input"
                    label="Current Balance"
                    defaultValue={userData.money}
                    InputProps={{
                      readOnly: true,
                    }}
                    variant="standard"
                  />
                </div>
              </Box>
            </Grid>

            {/* second grid */}
            <Grid
              item
              sm={6}
              sx={{
                height: "100%",
                display: { xs: "none", sm: "flex" },
                flexDirection: "column",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <BigProfile username={username}/>
              <Box display="flex" flexDirection="column" alignItems="center">
                <input type="file" onChange={handleFileChange} />
                <Button variant="contained" onClick={handleUpload}>
                  Upload
                </Button>
                {/* {selectedFile && <img src={URL.createObjectURL(selectedFile)} alt="Preview" />} */}
              </Box>
            </Grid>
          </Grid>
        </div>
      </ThemeProvider>
    );
  }

export default Profile;
