import { useState, useEffect } from 'react';
import axios from 'axios';

const defaultprof = {
  contentType: "image/jpg",
  data: "", // Convert to base64
}
function UserProfile({ username }) {
  const [avatar, setAvatar] = useState(defaultprof);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchAvatar = async () => {
      console.log("fetching avatar for " + username);
      try {
        const response = await axios.get(`http://localhost:3001/avatar/getavatar/${username}`);
        console.log("sent req");
        setAvatar({
          contentType: response.data.contentType,
          data: response.data.data,
          // data: Buffer.from(response.data.data).toString('base64'), // Convert to base64
        });
        // let base64 = Buffer.from(avatar.data).toString('base64');
        let base64 = btoa(avatar.data);
        console.log("base 64 will be"+base64);
        console.log("base64 image converted for" + username);
        setAvatar({
          contentType: avatar.contentType,
          data: base64,
        });
      } catch (error) {
      }
    };
    fetchAvatar();
  }, [username]);

  return (
    <div>
      {avatar && (
        <img
          src={`data:${avatar.contentType};base64,${avatar.data}`}
          alt="Avatar"
          style={{ height: '35px' }} // Set the desired height
        />
      )}
    </div>
  );
}

export default UserProfile;
