import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import Tooltip from '@mui/material/Tooltip';
import MenuItem from '@mui/material/MenuItem';
import DashInfoCard from './DashInfoCard';
import { useState, useEffect } from 'react';
import axios from 'axios';
import { Link} from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import UserProfile from './UserProfile';

const settings = ['Profile', 'Assets','Login'];

function ResponsiveAppBar() {
  const [adminview, setAdminview] = useState(false);
  const [anchorElNav, setAnchorElNav] = React.useState(null);
  const [anchorElUser, setAnchorElUser] = React.useState(null);
  const [communityCount, setCommunityCount] = useState(0);
  const navigate = useNavigate();

  const adminCheck = () => {
    const admintoken = localStorage.getItem('jwtToken');
    if (admintoken) {
      axios.post('http://localhost:3001/users/adminverify', {
        jwtToken: admintoken
      })
      .then((response) => {
        console.log(response.data);
        if (response.data.adminroute) {
          setAdminview(true);
        } else {
          setAdminview(false);
        }
      })
      .catch((error) => {
        console.log( error);
      });
    }
  }

  const handleLogout = () => {
    handleCloseUserMenu()
    // Clear the authentication token (e.g., from local storage)
    localStorage.removeItem('jwtToken');// Replace with your actual token storage mechanism
    localStorage.removeItem('USERID');
    
    navigate('/nothing');
 
    console.log('logout function ran now redirect to login')
    
  };
  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };
  const handleOpenUserMenu = (event) => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  useEffect(() => {
    async function fetchCommunityCount() {
      try {
        const response = await axios.get('http://localhost:3001/users/count');
        setCommunityCount(response.data.count);
      } catch (error) {
        console.error('Error fetching community count:', error);
      }
    }
    adminCheck();
  
    fetchCommunityCount();
  }, []);


  const activeLinkStyle = {
    borderBottom: '2px solid white', // Underline style for the active link
  };

  return (
    <AppBar position="static" sx={{ backgroundColor: '#10131A' }}>
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          <img
            src="/smartvitta.png" // Replace this with the actual path to your custom image
            alt="Smartvitta"
            style={{
              display: { xs: 'none', md: 'flex' },
              marginRight: '1rem',
              width: '32px', // Adjust the width and height as needed
              height: '32px',
            }}
          />
          <Typography
            variant="h6"
            noWrap
            component="a"
            href="/"
            sx={{
              mr: 2,
              display: { xs: 'none', md: 'flex' }, // Hide on mobile
              fontFamily: 'monospace',
              fontWeight: 700,
              letterSpacing: '.05rem',
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
            SmartVitta
          </Typography>
          <span
                style={{
                  width: '1px',
                  height: '32px',
                  backgroundColor: 'rgba(255, 255, 255, 0.12)',
                  margin: '0 1rem',
                }}
          />
          <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleOpenNavMenu}
              color="inherit"
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: 'block', md: 'none' },
              }}
            >
              <MenuItem onClick={handleCloseUserMenu} component={Link} to={'/'}>
                <Typography textAlign="center">Dashboard</Typography>
              </MenuItem>
              {adminview ? ( 
                <MenuItem onClick={handleCloseUserMenu} component={Link} to={'/admin'}>
                  <Typography textAlign="center">Admin Dashboard</Typography>
                </MenuItem>
              ) : null}
              <MenuItem onClick={handleCloseUserMenu} component={Link} to={'/myassets'}>
                <Typography textAlign="center">My Assets</Typography>
              </MenuItem>
              <MenuItem onClick={handleCloseUserMenu} component={Link} to={'/about'}>
                <Typography textAlign="center">About Us</Typography>
              </MenuItem>

              <MenuItem onClick={handleCloseUserMenu} component={Link} to={'/about'}>
                <Typography textAlign="center">Community Count: {communityCount}</Typography>
              </MenuItem>
             
            </Menu>
          </Box>
          <Typography
            variant="h5"
            noWrap
            component="a"
            href=""
            sx={{
              mr: 2,
              display: { xs: 'none', md: 'none' }, // Hide on mobile
              flexGrow: 1,
              fontFamily: 'monospace',
              fontWeight: 700,
              letterSpacing: '.3rem',
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
            Smartvitta
          </Typography>
          <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
          <Button
              onClick={handleCloseUserMenu}
              sx={{
                my: 2,
                color: 'white',
                display: 'block',
                fontFamily: 'LatoLight, sans-serif',
                fontSize: '15px',
                margin: 'auto 20px',
                textTransform: 'none',
              }}
            >
              Community Count: {communityCount}
            </Button>
            <Button
              onClick={handleCloseUserMenu}
              component={Link}
              to={'/'}
              sx={{
                my: 2,
                color: 'white',
                display: 'block',
                fontFamily: 'LatoLight, sans-serif',
                fontSize: '15px',
                margin: 'auto 20px',
                textTransform: 'none',
              }}
            >
              Dashboard
            </Button>
            {adminview ? (
              <Button
                onClick={handleCloseUserMenu}
                component={Link}
                to={'/admin'}
                sx={{
                  my: 2,
                  color: 'white',
                  display: 'block',
                  fontFamily: 'LatoLight, sans-serif',
                  fontSize: '15px',
                  margin: 'auto 20px',
                  textTransform: 'none',
                }}
              >
                Admin Dashboard
              </Button>
            ) : null}
            <Button
              onClick={handleCloseUserMenu}
              component={Link}
              to={'/Profile'}
              sx={{
                my: 2,
                color: 'white',
                display: 'block',
                fontFamily: 'LatoLight, sans-serif',
                fontSize: '15px',
                margin: 'auto 20px',
                textTransform: 'none',
              }}
            >
              Profile
            </Button>
            <Button
              onClick={handleCloseUserMenu}
              component={Link}
              to={'/about'}
              sx={{
                my: 2,
                color: 'white',
                display: 'block',
                fontFamily: 'LatoLight, sans-serif',
                fontSize: '15px',
                margin: 'auto 20px',
                textTransform: 'none',
              }}
            >
              About us
            </Button>
          </Box>

          <Box sx={{ flexGrow: 0, display: 'flex', alignItems: 'center' }}>

            {/* profile pic and the assets for the desktop view */}
          <DashInfoCard/>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
}
export default ResponsiveAppBar;
