import { Card, Container, Grid } from "@mui/material";
import LiveCard from "../components/LiveCard";
import BtcCard from "../components/BtcCard";
import FinCard from "../components/FinCard";
import USDCard from "../components/USDCard";
import GoldCard from "../components/GoldCard";
import VideoCard from "../components/Videocard";
import Blogcards from "../components/Blogcards";
import MarketNews from "../components/MarketNews";
import "../App.css";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";
import { styled } from "@mui/material/styles";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import ListSubheader from "@mui/material/ListSubheader";
import Divider from "@mui/material/Divider";
import {Typography} from "@mui/material";
import MoneyControl from "../components/MoneyControl";
import MotivationBar from "../components/MotivationBar";
import LeftGrid from "../components/LeftGrid";

const Dashboard = () => { 
  return (
    <Box className="App">
      <Container maxWidth={false}>
        {/* <Grid container spacing={2} sx={{ height: "100%" }}> */}
        <Grid container spacing={2}>
       

       {/* grid for the left starts here */}
          <Grid
            item
            justifyContent="center"
            alignItems="center"
            lg={3}
            md = {4}
            xs={12}
            spacing={0}
            
            container
            sx={{ height: "98vh" ,  display: { xs: 'none', md: 'block' , marginTop : '8px'} }}
            
          >
            <Grid
              item
              xs={12}
              sx={{
                background: "#111A30",
                height: "47.5%",
                overflow: 'hidden',
                borderRadius: "10px",
                padding: "8px",
               
              }}
            >
              <p className="mb-2  font-bold text-zinc-400">Moneycontrol News</p>
              <Box className="scrollbox"  sx={{ overflowY : 'hidden'}}>
                <List
                  sx={{
                    position: "relative",
                    overflow: "auto",
                    maxHeight: 300,
                    "& ul": { padding: 0 },
                    overflow: 'auto'
                  
                  }}
                  subheader={<li />}
                >
                  <MoneyControl/>
                
                  
                </List>
              </Box>
            </Grid>
            <Grid
              item
              xs={12}
              sx={{
                height: "47.5%",
                background: "#111A30",
                borderRadius: "10px",
                padding: "8px",
                overflow: "hidden",
                marginTop : '10px'
              }}
            >
              <p className="mb-2 font-bold text-zinc-400">Blogs</p>
             <Box className="scrollbox">
                <List
                  sx={{
                    position: "relative",
                    overflow: "auto",
                    maxHeight: 300,
                    "& ul": { padding: 0 },
                   
                  }}
                  subheader={<li />}
                >
                  <Blogcards/>
                </List>
              </Box>
            </Grid>
          </Grid>


          {/* grid for the central part starts here */}

          <Grid    item lg={7}  md={8} xs={12}   justifyContent='center' alignItems='center' spacing={0}  sx={{ height: "98vh" , }} container>
            {/* grid for the central analytics and videos */}
            <Grid item xs={12}  sx={{borderRadius: '10px', height : '56.6%' ,padding:'10px',backgroundColor : '#1A243C'}} >
             <Box style={{height : "95%"}} >
            <Box className="center-content">
              <Box className="horizontal-scroll  ">
                <LiveCard name="NIFTY 50" code="3BNSX" />
                <LiveCard name="BANK NIFTY" code="3Bnbx" />
                <FinCard name="FIN NIFTY" code="apicurrency" />
                <GoldCard name="GOLD" code="goldRate" />
                <BtcCard name="BTC/INR" code="btcinr" />
                <USDCard name="USD/INR" code="USDINR" />
              </Box>
              </Box>
                
              <Box className="videoDiv" style={{height : '80%'}} >
                          <VideoCard/>
              </Box>
              </Box>
            </Grid>
  
           {/* card for the news */}
           <Grid item xs={12} sx={{height : '38.4%', padding : '5px','backgroundColor' : '#111B30','borderRadius' : '10px','display' : 'flex' ,  'flexDirection' : 'column','justifyContent' : 'center' }} >
           <p className="market mt-1 mb-2">Market News</p>
            <Box className="scrollbox" >
                <List
                  sx={{
                    position: "relative",
                    overflow: "auto",
                    maxHeight: 300,
                    "& ul": { padding: 0 },
                  }}
                  subheader={<li />}
                >
                  {/* {[0, 1, 2, 3, 4].map((sectionId) => (
                    <li key={`section-${sectionId}`}>
                      <ul>
                        {[0, 1, 2].map((item) => (
                          <ListItem key={`item-${sectionId}-${item}`}>
                            <ListItemText primary={`Item ${item}`} />
                          </ListItem>
                        ))}
                      </ul>
                      <Divider orientation="horizontal" flexItem />
                    </li>
                  ))} */}
                  <MarketNews  />
                </List>
              </Box>
            </Grid>
          </Grid>


          {/* this is just for checking if the left bar goes to bottom */}
      <LeftGrid/>




{/* grid for the right starts here */}
          <Grid item lg={2} xs={12} className="  hidden  xl:block" sx={{borderRadius: '10px',height : '98vh',marginTop : '8px'}}>
            {/* Right Sidebar */}
            {/* <Box className="sidebar" style={{ height: "100%" }}>
              Right Sidebar (Motivation here)
            </Box> */}
            <MotivationBar />
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
};

export default Dashboard;
