import React, { useEffect, useState } from 'react';
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import Divider from "@mui/material/Divider";
import Skeleton from "@mui/material/Skeleton";

function Blogcards() {
  const [blogPosts, setBlogPosts] = useState([]);
  const [loading, setLoading] = useState(true);

  // Function to get a random image URL from /public/icons
  const getRandomIcon = () => {
    const icons = [
      '/blog/1.jpg',
      '/blog/2.png',
      '/blog/3.png',
      '/blog/4.png',
      // Add more icon paths as needed
    ];
    const randomIndex = Math.floor(Math.random() * icons.length);
    return icons[randomIndex];
  };

  useEffect(() => {
    fetch(`http://localhost:3001/blogs/recentblogs`) // Replace with your API endpoint
      .then((response) => response.json())
      .then((data) => {
        setBlogPosts(data); // Update the state with fetched data
        setLoading(false); // Set loading to false after data is fetched
      })
      .catch((error) => console.error("Error:", error));
  }, []);

  return (
    <List >
      {loading ? (
        // Show skeleton while loading
        Array.from({ length: 5 }).map((_, index) => (
          <React.Fragment key={`skeleton-${index}`}>
            <ListItem>
              <ListItemText>
                <Skeleton animation="wave" height={30} />
              </ListItemText>
            </ListItem>
            {index !== 4 && <Divider orientation="horizontal" flexItem />}
           
          </React.Fragment>
        ))
      ) : (
        // Show data after loading is complete
        blogPosts.map((post, index) => (
          <React.Fragment key={`post-${index}`}>
            {/* <ListItem button component="a" href={post.url} target="_blank" rel="noopener noreferrer">
              <ListItemText primary={post.title} primaryTypographyProps={{ style: { fontSize: '14px' } }} />
            </ListItem> */}
            <ListItem button component="a" href={post.url} target="_blank" rel="noopener noreferrer">
              <img
                src="/images/smartvitta.png" // Replace this with the actual path to your custom image
                alt="Smartvitta"
                style={{
                  display: { xs: 'none', md: 'flex' },
                  marginRight: '1rem',
                  width: '42px', // Adjust the width and height as needed
                  height: '42px',
                  backgroundColor: '#343C4F',
                  borderRadius: '50%',
                  padding: '4px',
                }}
              />
              <ListItemText primary={post.title} primaryTypographyProps={{ style: { fontSize: '14px' } }} />
            </ListItem>
            {index !== blogPosts.length - 1 && <Divider orientation="horizontal" flexItem />}
            <Divider orientation="horizontal" flexItem style={{ backgroundColor: '#343C4F' }}/>
          </React.Fragment>
        ))
      )}
    </List>
  );
}

export default Blogcards;
