import React, { useEffect, useState } from "react";
import { Typography } from "@mui/material";
import Card from "@mui/material/Card";

const LiveCard = ({ name, code }) => {
  const [value, setValue] = useState(0);
  const [isPriceIncreased, setIsPriceIncreased] = useState(false);

  useEffect(() => {
    // Fetch data from the API using the provided URL
    if (code !== "null") {
      const fetchData = () => {
        fetch(`http://localhost:3001/api/${code}`)
          .then((response) => response.json())
          .then((data) => {
            const currentPrice = Math.trunc(data.data.pricecurrent);
            if (currentPrice > value) {
              setIsPriceIncreased(true);
              setTimeout(() => setIsPriceIncreased(false), 1000); // Set blinking time to 1 second
            }
            setValue(currentPrice);
          })
          .catch((error) => console.error("Error fetching data:", error));
        };

      // Fetch data initially
      fetchData();

      // Fetch data every 5 seconds
      // const intervalId = setInterval(fetchData, 5000);
      const intervalId = setInterval(fetchData, 5000);

      // Clean up the interval when the component unmounts
      return () => clearInterval(intervalId);
    }
  }, [code, value]);

  return (
    <Card
    className="cardComponent"
      variant="outlined"
      sx={{
        width: {  xs : "140px", lg: "300px" },
        height: "auto",
        ml: "10px",
        mr: "10px",
        height: "55px",
        // textAlign: "center",
        display: "flex",
        flexDirection: "column",
        // alignItems: "center",
        paddingLeft : '15px',
        justifyContent: "center",
        backgroundColor: "#1A243C",
        color: isPriceIncreased ? "green" : "white",
        borderRadius: "8px",
        border: '1px solid #5F5F5F',
        
      
      }}
    >
      <div
        // style={{
        //   padding: "10px",
        //   display: "flex",
        //   flexDirection: "column",
        //   justifyContent: "space-between",


        // }}
        className="cardcontent p-2.5 flex flex-col "
      >
      <p className="text-sm" style={{fontSize : '13px'}}>{name}</p>
        <p className="text-sm " style={{fontSize :'11px', color : '#C6C6C6'}}>{value}</p>
      </div>
    </Card>
  );
};

export default LiveCard;
