import React, { useState, useEffect } from 'react';

const MotivationBar = () => {
  const [imageIndex, setImageIndex] = useState(1);
  const [quote, setQuote] = useState('');

  useEffect(() => {
    const imageChangeInterval = setInterval(() => {
      setImageIndex((prevIndex) => {
        if (prevIndex === 3) {
          return 1;
        } else {
          return prevIndex + 1;
        }
      });
    }, 20000); // Change image every 20 seconds

    const fetchQuote = async () => {
      try {
        const response = await fetch('http://localhost:3001/quote/randomquote');
        const data = await response.json();
        setQuote(data.quote);
      } catch (error) {
        console.error('Error fetching quote:', error);
      }
    };

    fetchQuote(); // Fetch initial quote
    const quoteInterval = setInterval(fetchQuote, 20000); //(20000) Fetch new quote every 20 seconds

    return () => {
      clearInterval(imageChangeInterval);
      clearInterval(quoteInterval);
    };
  }, []);

  return (
    <div
      className="sidebar"
      style={{
        height: '100%',
        backgroundImage: `url(/images/rook${imageIndex}.png)`,
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat',
        position: 'relative',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        flexDirection: 'column',
        borderRadius: '10px',
      }}
    >
      <div
        style={{
          color: 'white',
          textShadow: '0px 2px 4px rgba(0, 0, 0, 0.6)',
          fontWeight: 'bold',
          fontSize: '20px',
          lineHeight: '1.5',
          textAlign: 'center',
        }}
      >
        {quote}
      </div>
    </div>
  );
};

export default MotivationBar;
