import { Typography } from "@mui/material";
import React from "react";
import SimpleImageSlider from "react-simple-image-slider";
import Slider from "react-slick";
import AwesomeSlider from 'react-awesome-slider';
import 'react-awesome-slider/dist/styles.css';
import withAutoplay from 'react-awesome-slider/dist/autoplay';
import { useState , useEffect , useRef } from "react";
import axios from 'axios';
// import { Swiper, SwiperSlide } from 'swiper/react';


// import 'swiper/swiper-bundle.min.css';
import { Swiper, SwiperSlide } from 'swiper/react';

// SwiperCore.use([Navigation, Pagination, Autoplay]);

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/effect-coverflow';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import { EffectCoverflow, Pagination, Navigation ,  Autoplay } from 'swiper/modules';




const AutoplaySlider = withAutoplay(AwesomeSlider);
const slideImages = [
  {
    url: "https://images.unsplash.com/photo-1506710507565-203b9f24669b?ixlib=rb-1.2.1&auto=format&fit=crop&w=1536&q=80",
    caption: "Slide 2",
  },
  {
    url: "https://images.unsplash.com/photo-1536987333706-fc9adfb10d91?ixlib=rb-1.2.1&auto=format&fit=crop&w=1500&q=80",
    caption: "Slide 3",
  },
];






const startupScreen = (
  <div>
    <img src={slideImages[0].url} />
  </div>
);



const Videocard = () => {


  const [videos, setVideos] = useState([]);
  const [videolength, setVideoLength] = useState()
  const swiperRef = useRef(null);
  // defining the array of the youtubeVideos

  const clickNextArrow = () => {
    // if (swiperRef.current && swiperRef.current.swiper) {
    //   swiperRef.current.swiper.slideNext();
    // }
    document.querySelector('.nextslide').click()
  }

  useEffect(() => {
    axios.get('http://localhost:3001/videos/getvideos')
      .then((response) => {
        setVideos(response.data);
        setVideoLength(response.data.length); // Set the correct video length
      })
      .catch((error) => {
        console.log('Error fetching videos:', error);
      });

    // Start the interval for auto slide transition
    const timer = setInterval(clickNextArrow, 2000);
    
    // Clear the interval when the component unmounts
    return () => clearInterval(timer);
  }, []); 


  function getVideoIdFromUrl(url) {
    // Extract video ID from URL
    const urlParts = new URL(url);
    const videoId = urlParts.searchParams.get('v');
    console.log('video id is',videoId)
    return videoId;
  }


  const slidesPerView = window.innerWidth >= 800 ? 2 : 1;


  return (
    <div className="imagesliderclass">

      <Swiper
     
        style={{ position: 'relative', zIndex: 1 }}
        effect={'coverflow'}
        grabCursor={true}
        centeredSlides={true}
        loop={false}
        initialSlide={1}
      
        // loopAdditionalSlides={videolength} // Adjust the number of duplicated slides
        slidesPerView= {slidesPerView}
        coverflowEffect={{
          rotate: 0,
          stretch: 0,
          depth: 100,
          modifier: 2.5,
        }}
        pagination={{ el: '.swiper-pagination', clickable: true }}
        navigation={{
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
          clickable: true,
        }}
        modules={[EffectCoverflow, Pagination, Navigation]}
        className="swiper_container"
        
 
      >
        {videos.map((video, index) => (
          <SwiperSlide key={index}   zoom={true}  onClick={()=>{  window.open(video.videoUrl, '_blank');}}>
            <img
            className="videoimage"
              src={`https://img.youtube.com/vi/${getVideoIdFromUrl(video.videoUrl)}/0.jpg`}
              alt={video.caption}
              style={{
                width: "100%",
                height: "250px", 
                objectFit : "fill",
                borderRadius: "10px",
                cursor : 'pointer'
               
              }}
              // className="object-fill"
             
            />
          </SwiperSlide>
    
        ))}

        <div className="slider-controler">
          <div className="swiper-button-prev slider-arrow">
           <ion-icon name="arrow-back-outline"></ion-icon> 
          </div>
          <div className="swiper-button-next slider-arrow">
            <ion-icon name="arrow-forward-outline"></ion-icon>
          </div>
          <button className="nextslide"><div className="swiper-pagination"></div> </button>
        </div>
      </Swiper>
      
     




    </div>
  );
};

export default Videocard;



