import React, { useEffect, useState } from "react";
import { Typography } from "@mui/material";
import Card from "@mui/material/Card";

const BtcCard = ({ name, code }) => {
  const [value, setValue] = useState(0);
  const [isPriceIncreased, setIsPriceIncreased] = useState(false);

  useEffect(() => {
    // Fetch data from the API using the provided URL
    if (code !== "null") {
        const fetchData = () => {
            fetch(`http://localhost:3001/apicrypto/${code}`)
              .then((response) => response.json())
              .then((data) => {
                const lastPriceString = data.data.lastPrice;
                const currentPrice = parseFloat(lastPriceString.replace(/,/g, '')); // Remove commas and convert to number
                
                if (!isNaN(currentPrice)) {
                  if (currentPrice > value) {
                    setIsPriceIncreased(true);
                    setTimeout(() => setIsPriceIncreased(false), 1000);
                  }
                  setValue(currentPrice);
                } else {
                  console.error("Invalid current price:", lastPriceString);
                }
              })
              .catch((error) => console.error("Error fetching data:", error));
          };          

      // Fetch data initially
      fetchData();

      // Fetch data every 5 seconds
      // const intervalId = setInterval(fetchData, 5000);
      const intervalId = setInterval(fetchData, 5000);

      // Clean up the interval when the component unmounts
      return () => clearInterval(intervalId);
    }
  }, [code, value]);

  return (
    <Card
      variant="outlined"
      sx={{
        width: {  xs : "140px", lg: "300px" },
        height: "auto",
        ml: "10px",
        mr: "10px",
        height: "55px",
        // textAlign: "center",
        display: "flex",
        flexDirection: "column",
        // alignItems: "center",
        paddingLeft : '15px',
        justifyContent: "center",
        backgroundColor: "#1A243C",
        color: isPriceIncreased ? "green" : "white",
        borderRadius: "8px",
        border: '1px solid #5F5F5F',
        // padding : '5px'
      }}
    >
      <div
        style={{
          // padding: "12px",
          display: "flex",
          flexDirection: "column",
          justifyContent: "space-between",
        }}
        className="cardcontent  flex flex-col "
      >
         <p  style={{fontSize : '13px' }}>{name}</p>
        <p className="text-sm " style={{fontSize :'11px', color : '#C6C6C6'}}>{value}</p>
      </div>
    </Card>
  );
};

export default BtcCard;
